import { createSlice } from "@reduxjs/toolkit";

// 1. slice
const counter2Slice = createSlice({
    name: 'counter2',
    initialState: {
        v:100
    },
    reducers: {
        counterAdd(state) {
            state.v +=1
        }
    }
})

export const {counterAdd} = counter2Slice.actions //2. sliceactionsactionsslice

export default counter2Slice.reducer //3.. reducerstoreslice